import navtop from "../../components/nav_top/nav_top.vue"
export default {
	data() {
		return {
			navtop: {
						title: '四川弘浩物流',
						color: 'black'
					},
					infos:{},
			startinfo:'成都市武侯区新希望大厦7楼704室',
			endinfo:'成都市武侯区新希望大厦',
			time:'2019-08-07 11:30',
			type:'玻璃制品',
			num:'艺术品/50箱',
			size:'30*30*30cm',
			creattime:'2019-08-07 11:30',
			ordernum:'aeqweqwd484sd894',
			code:'1565sd',
			number:''
		}
		

	},
	components: {
		navtop
	},
	onLoad:function(option){//opthin为object类型，会序列化上页面传递的参数
	this.number=option.number
	this.requst(this.number)
		console.log(option.number);//打印出上页面传递的参数
	},
	methods: {
		tocall(tel){
			uni.makePhoneCall({
				phoneNumber:tel,
				success: (res) => {
				},
				fail: (res) => {
					console.log(res);
					uni.showToast({
					    title: "拨打失败，可手动拨打电话",
					    icon: "none"
					})
				}
			})
		},
		toonline(){
			uni.navigateTo({
				url:'../online/online'
			})
		},
		requst(number){
			uni.request({
				url: 'http://hhwl.com/wxapp/login/getHh',
				method: 'POST',
				data: {number:number,type:1},
				success: res => {
					if(res.data.data.result==1){
						uni.showToast({
							title:res.data.data.msg,
							icon: "none"
						})
					}else if(res.data.data.result==0){
						console.log(res.data.data);
						this.infos=res.data.data.msg
						console.log(this.infos);
						
					}
				},
				fail: () => {},
				complete: () => {}
			});
		}
	}
}
